@import './animate.css';
$timeunit: 300ms;
.body{
    overflow: hidden;
}
.clearfix::after{
    content:'';
    clear:both;
    display: block;
}


.blink-animation-container{
    margin:auto;
    // float: left;
    width: 700px;
    height: 400px;
    background-color: #fff;
    display: flex;
    flex-direction: row;
    align-items: center;
    // justify-content: space-evenly;
    overflow: hidden;
    position: relative;
    // border: 1px solid seagreen;
    animation: turnBlack 0ms ease 3900ms forwards,changeWhite1 0ms ease 6900ms forwards, turnBlack 0ms ease 9250ms forwards,changeWhite1 0ms ease 14150ms forwards, changeHeight 1500ms ease-in-out 19.5s forwards  ;
    .blink-animation{
        margin:auto;
        width: 60%;
        // background-color: floralwhite;
        height: 100px;
        span{
            font-weight: 900;
            font-size: 60px;
            font-family: Arial,Helvetica, sans-serif;
            position: absolute;
            transform:translateY(-2000px);
        }
        &-0{ 
            //  display: inline-block;
            color:#222222;
             animation: scaleDown 400ms ease forwards, moveRight 800ms ease-out 400ms forwards, disappear 0ms ease-in-out 1150ms forwards;
            }
    
        &-1{    color:#222222;
            padding-left: 310px;
            animation: moveRight 800ms ease-out 400ms forwards, disappear 0ms ease-in-out 1150ms forwards;
            
            }
        &-2{
            color:#222222;
            animation: appear 400ms 700ms forwards, moveRightTitle 800ms ease-out 400ms forwards, biggerSudden 600ms step-end 1150ms forwards, rollDown 400ms ease-in-out 1900ms forwards;
            }
        &-3{
            color:#222222;
            animation: rollDownCat 400ms ease-in-out 1800ms forwards;
            
        }
        &-4{
            color: #fff;
            animation: appear 0ms ease 4000ms forwards, disappear  0ms ease 4300ms forwards;
        }
        &-5{
            padding-left: 150px;
            color: #fff;
            animation: appear 0ms ease 4250ms forwards, disappear  0ms ease 4500ms forwards;
        }
        &-6{
            padding-left: 50px;
            color: #fff;
            animation: scaleDown 400ms ease-in-out 4500ms forwards,moveRightStay 400ms ease-out 4900ms forwards,disappear 0ms ease 6900ms forwards;
        }
        &-7{
            padding-left: 270px;
            color: #fff;
            animation: moveRightStay 400ms ease-out 4900ms forwards,disappear 0ms ease 6900ms forwards;
        }
        &-8{color: #222222;
            padding-left: 30px;
            font-family:Segoe Script!important;
            font-style: italic;
            font-weight: 900;

            animation: rollDownTags1 400ms ease-out 5300ms forwards, rollDownTags2 400ms ease-in 6100ms forwards;
        }
        &-9{ padding-left: 5px;
            // font-size: 3.5rem;
            font-family:Segoe Script!important;
            font-style: italic;
            font-weight: 900;

            animation: rollDownTags1 400ms ease-out 6050ms forwards, disappear 0ms ease 6900ms forwards;
        }
        &-10{
            animation: appear 0ms ease 7150ms forwards,fadeOutDown 300ms ease 7700ms forwards;
        }
        &-11{
            padding-left: 170px;
            animation: lightSpeedInRight 500ms ease 7300ms forwards,fadeOutDown 300ms ease 7700ms forwards;
        }
        &-12{
            padding-left: 30px;
            // transform: scale(6);
            animation:   tada1 1500ms ease-in-out 7800ms;
        }
        &-13{
            color: #fff;
            animation: appear 0ms 9500ms forwards,disappear 0ms 9700ms forwards ;
        }
        &-14{
            color: #fff;
            animation: appear 0ms 9690ms forwards,disappear 0ms 9900ms forwards ;
        }
        &-15{
            color: rgb(245, 245, 90);
            animation: appear 0ms 9890ms forwards,disappear 0ms 10100ms forwards ;
        }
        &-16{
            color:rgb(0, 152, 247);
            animation: appear 0ms 10090ms forwards,disappear 0ms 10300ms forwards ;
        }
        &-17{
            color: #fff;
            animation: appear 0ms 10290ms forwards,disappear 0ms 10500ms forwards ;
        }
        &-18{
            color: #fff;
            animation: appear 0ms 10490ms forwards,disappear 0ms 10700ms forwards ;
        }
        &-19{
            position: absolute;
            color: #fff;
            animation: appear 0ms 10690ms forwards,moveRightStay2 1500ms ease-in-out 10690ms forwards,disappear 0ms 12500ms forwards ;
        }
        &-20{ 
            font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif!important;
            font-size: 3rem!important;
            font-weight: 600!important;
            padding-top: 30px;
            left: 40px;
            color: #fff;
            animation: appear 0ms 12500ms forwards, changgeGreen 200ms 12800ms,fadeOutDownBig 500ms ease-in 13900ms forwards}
        &-21{ color: #fff;
            left: 220px;
            padding-top: 30px;
            font-weight: 600!important;
            font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif!important;
            font-size: 3rem!important;
            animation: appear 0ms 12500ms forwards,changgeBlue 200ms 13000ms,fadeOutDownBig 500ms ease-in  13900ms forwards}
        &-22{ color: #fff;
            left: 340px;
            padding-top: 30px;
            font-weight: 600!important;
            font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif !important;
            font-size: 3rem!important;
            animation: appear 0ms 12500ms forwards,changgeYellow 200ms 13200ms,fadeOutDownBig 500ms ease-in 13900ms forwards }
        &-23{ color: #fff;
            left: 460px;
            padding-top: 30px;
            font-weight: 600!important;
            font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif!important;
            font-size: 3rem!important;
            animation: appear 0ms 12500ms forwards,changgeRed 200ms 13400ms,fadeOutDownBig 500ms ease-in 13900ms forwards ;}
        &-24{ color: #fff;
            left: 570px;
            padding-top: 30px;
            font-weight: 600!important;
            font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif!important;
            font-size: 3rem!important;
            animation: appear 0ms 12500ms forwards, changgePurple 200ms 13600ms,fadeOutDownBig 500ms ease-in  13900ms forwards ;
        }
        &-25{
            
            transform: scale(0.5);
            animation: fadeInDownBig 500ms 13700ms forwards,disappear 0ms 16200ms forwards ;
        }
        &-26{
            padding-left: 150px;
            animation: fadeInRight 200ms ease-in-out 16200ms forwards,fadeOutLeft 200ms ease-in 16600ms forwards;
        }
        &-27{
            padding-left: 150px;
            animation: fadeInRight 200ms ease-in-out 16600ms forwards,fadeOutLeft 200ms ease-in 17000ms forwards;
        }
    &-28{
        padding-left: 150px;
        animation: fadeInRight 200ms ease-in-out 17000ms forwards,fadeOutLeft 200ms ease-in 17400ms forwards;
    }
    &-29{
        padding-left: 130px;
        animation: appear 0ms 17550ms forwards, tada 1000ms 17550ms forwards, zoomOutDown 800ms 18400ms forwards  ;
    }
    &-30{
        position: absolute;
        left: 40px;
        padding-top:20px;
        // padding-left: -1000px;
        font-size: 4rem!important;
        animation: fadeInDown 500ms ease-in-out 18600ms forwards;
    }
        .nodejs-icon{
            transform: scale(0.5);
            position: absolute;
            // padding-left: 30px;
            left: 50px;
            top: -100px;
            animation:biggerSudden2 400ms step-end 14500ms forwards,scaleDown2 2200ms ease-in-out 15000ms forwards;
        }
        .react-icon{
            position: absolute;
            // padding-left: 30px;
            left: 200px;
            top: -10px;
            width: 100px;
            height: 100px;
            // background-color: lime;
            animation:  appear 0ms 10690ms forwards, rotateReact 4000ms linear 10690ms  10 forwards,disappear 0ms 12500ms forwards ; 
        }
        .together1{
            width: 100%;
            height: 100%;
            // background-color: lightcoral;
            // overflow: hidden;
            animation: overflowHidden 0ms ease 1800ms forwards, hinge 600ms ease-in 3100ms forwards, overflowShow 0ms ease 3950ms forwards ;
        }
        .together2{
            transform: translateY(1000px);
            width: 70%;
            height: 100%;
            margin-left: 230px;
            background-color: rgb(255, 255, 70);
            animation: appear 0ms ease 5250ms forwards, changeColor 200ms ease 6100ms forwards,disappear 0ms ease 6900ms forwards,overflowShow 0ms ease 6900ms forwards;
            position: relative;
            border-radius: 20px;
        }
    }
}
@keyframes changeHeight {
    0%{
        height: 400px;
    }
    100%{
        height: 200px;
    }
}
@keyframes scaleDown2{
    0%{
        transform:  scale(1.2);
    }
    100%{
        transform:  scale(1);
    }
}
@keyframes biggerSudden2{

    0%{  transform:  scale(0.5);
    }
    50%{
        transform:  scale(3);
    }
    70%{
        transform:  scale(1.2);
    }  100%{
        transform:  scale(1.2);
    }

}
@keyframes scaleUp2 {
    0%{
        transform: scale(1);
    }
    100%{
        transform: scale(4);
    }
}
@keyframes changeWhite1 {
    0%{
        color: #fff;
    }
    100%{
        color: #fff;
    }
}
@keyframes changgeGreen {
    0%{
        color: seagreen;
    }
    100%{
        color: seagreen;
    }
}
@keyframes changgeBlue {
    0%{
        color: skyblue;
    }
    100%{
        color: skyblue;
    }
}
@keyframes changgeYellow {
    0%{
       color: rgb(255, 255, 83);
    }
    100%{
        color: rgb(255, 255, 83);
    }
}
@keyframes changgeRed {
    0%{
        color: rgb(250, 73, 73);
    }
    100%{
        color: rgb(250, 73, 73);
    }
}
@keyframes changgePurple{
    0%{
        color: rgb(156, 0, 156);
    }
    100%{
        color: rgb(156, 0, 156);
    }
}
@keyframes moveRightStay2 {
    0%{ transform:translateY(0); }

    100%{
       
        transform:translateX(-20px);
    }
}
@keyframes rotateReact {
    0%{
        transform: rotate(0deg);
    }
    100%{
        transform: rotate(360deg);
    }
}
@keyframes tada1 {
    0% {
 
    //   transform:scale(6) rotate3d(0, 0, 1, 5deg);
    }
  
    10%,
    20% {
  
      transform:scale(6) rotate3d(0, 0, 1, -5deg);
    }
  
    30%,
    50%,
    70%,
    90% {

      transform:scale(6)rotate3d(0, 0, 1, 5deg);
    }
  
    40%,
    60%,
    80% {
    
      transform:scale(6) rotate3d(0, 0, 1, -5deg);
    }
  
    100%{
    //   transform:scale(6) rotate3d(0, 0, 1, 5deg);
    }
  }
@keyframes scaleUp {
    0%{
    transform: scale(1);
    }
    100%{   
    transform: scale(6);
    }
}

@keyframes changeColor {
    0%{ background-color: rgb(255, 255, 70);}
    100%{
        background-color:lightcoral ;
    }
}

@keyframes rollDownTags1 {
    0%{ transform: translateY(-120px);}
    100%{
        // padding-left: -310px;
        transform: translateY(0px);
    }
}
@keyframes rollDownTags2 {
    0%{ transform: translateY(0px);}
    100%{
        // padding-left: -310px;
        transform: translateY(120px);
    }
}
@keyframes moveRightStay {
    0%{ transform:translateY(0); }

    100%{
       
        transform:translateX(-140px);
    }
}
