@keyframes turnBlack {
    0%{background: #222222;}
    100%{
        // padding-left: -310px;
       background: #222222;
    }
}
@keyframes changeWhite1{
    0%{background: #fff;}
    100%{
        // padding-left: -310px;
       background: #fff;
    }
}
@keyframes rollDown {
    0%{}
    100%{
        // padding-left: -310px;

        transform: translate(-390px,120px);
    }
}
@keyframes rollDownCat {
    0%{
        transform: translateY(-120px);
    }
    100%{
     
        transform: translateY(0px);
    }
}

@keyframes scaleDown{
    0%{   transform:scale(4);}
    100%{
        transform:scale(1);
    }
}

@keyframes moveRight{
    0%{ transform:translateY(0); }
    60%{
        opacity: 1;
    }
    100%{
        opacity: 0;
        transform:translateX(-390px);
    }
}
@keyframes moveRightTitle{
    0%{ padding-left: 390px }
    60%{
    
    }
    100%{padding-left: 390px;
        transform:translateX(-390px);
    }
}
@keyframes biggerSudden{
    0%{ padding-left: 0px;
        transform:translateX(0px);
    }
    50%{
       
        transform:  scale(4);
    }
    70%{
        transform:  scale(1);
    }
}
@keyframes stay{
    0%{
        transform: translate3d(0, 0, 0);
    }
    100%{
        transform: translate3d(0, 0, 0);
    }
}
@keyframes overflowHidden {
    0%{
        position: relative;
        overflow: hidden;
    }
    100%{
        position: relative;
        overflow: hidden;
    }
}
@keyframes overflowShow {
    0%{
        position: absolute;
        overflow:visible;
    }
    100%{
        position:absolute;
        overflow:visible;
    }
}
@keyframes disappear{
    0%{     
        transform: translateY( -1000px);}
    100%{
   
        transform: translateY(-1000px);
    }
}

@keyframes appear{
    0%{     
        transform: translateY( 0px);
    }
    100%{
   
        transform: translateY(0px);
        // opacity: 0;
    }
}

