
    .editBar{
        transition: all 300ms ease;
        flex-direction: column;
        align-content: space-between;
        width: 4rem;
        background-color: #222222;
        position: fixed;
        right:15%;
        top:30rem;
        border-radius: 0.8rem;
        .editButton:hover {
            color:lightgreen;
        }
        .editButton{
          width: 4rem;
          height: 4rem;
          background-color: rgba(0,0,0,0);
          border-style: none;
          color: #fff;
            span{
                text-align: center;
                line-height: 4rem;
                display: block;
                width: 100%;
                height: 100%;
            }
        }
        .editbar-link-input{
            width: 20rem;
            position: absolute;
            // background-color: #222222;
            z-index: 1;
            top: 25rem;
            left: -4.5rem;
            height: 3rem;
            input{
                float: left;
                background-color: #222222;
                border-style: none;
                color:#fff;
                width: 14rem;
                height: 3rem;
                border-radius: 2rem 0 0 2rem;
            //   outline: 0;
                padding-left: 1rem;
                padding-right: 0.5rem;
                // visibility:hidden;
            }
            button{
                float: left;
                border-radius: 0 2rem 2rem 0;
                border-style: none;
                height: 3rem;
                width: 6rem;
                background-color: #444444;
                color: #fff;
                font-size: 1.4rem;
                cursor: pointer;
            }
        }
    }