@import '../../assests/_variables.scss';
$mid-height:4rem;

.clearfix::after{
    content: "";
    padding: 0;
    clear: both;
    display: block;
}

@keyframes expand {
    0%{
        transform: scaleX(0);
    }
    100%{
        transform: scaleX(1);
    }
}

.topbar {
    width: 100%;
    height: 6.5rem;
    position: fixed;
    top: 0;
    left: 0;
    background-color: #fff;
    // border: 1px solid green;
z-index: 100;
   box-shadow:0 0 15px 0 rgba(0,0,0,0.3) ;
    // three width
    .ba{
        width: 100%;
        max-width: $max-width;
        height:100%;
        // background-color: pink;
        margin: auto;
        padding: 0 5rem 0 5rem;
        .ca{
               width: 100%;
               height: 100%;
            //    background-color: lightblue;
               padding-top:1rem;
        
              .da{
                width: 100%;
                height: $mid-height;
                // background-color: green;
                padding: 0;
         position: relative;
                    .ea{
                        display: block;
                        width: 30%;
                        height: 100%;
                        line-height: $mid-height*1.13;
                        float: left;
                        .eaa:hover{
                            background-color: #222222;
                            color: #fff;
                        }
                            .eaa{
                                // transform: translateY(1rem);
                                text-decoration: none;
                                font-size: 3.2rem;
                                font-weight: 700;
                                // padding-left: 1rem;
                                // padding-right: 1rem;
                        }
                   
                    }

                    .eb{
                        padding-top: 0.5rem;
                        height: 100%;
                        width: 70%;
                        // background-color: purple;
                        float: right;
                            .eba{
                               width: 100%;
                               height: inherit;
                                // background-color: lightcyan;
                                .topbar-nav{
                                    padding-right: 1rem;
                                    height: inherit;
                                    width: 38rem;
                                    margin-right: 1rem;
                                    float: right;
                                    display: flex;
                                    flex-direction: row;
                                    flex-wrap: nowrap;
                                    justify-content: space-evenly;
                                    transition: all 0.3s ease;
                                    .ebaaa{
                                        display: block;
                                        width: 25%;
                                        height: 100%;
                                        
                                        // background-color: orange;
                                        text-decoration: none;
                                        font-family:medium-content-sans-serif-font, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif ;
                                        font-weight: map-get($font-weights, bold);
                                        font-size: 1.6rem;
                                        line-height: $mid-height;
                                        color: $subtext-color;
                                        // background-color: #fff;
                                        text-align: center;
                                        transition: all 0.3s ease;
                                        // overflow: hidden;
                                        span{
                                            display: block;
                                            width: 100%;
                                            height: 20%;
                                            // background-color: #222222;
                                            animation: expand 300ms ease-in-out forwards;
                
                                        }
                                    }
                                    .ebaaa:hover{
                                        background-color: #222222;
                                        color: #fff;
                                    }
                                }
                                .ebab{
                                 
                                    height: inherit;
                                    width: 5rem;
                                    // background-color: lightskyblue;
                                    float: right;
                            
                                    a{
                                        height: 100%;
                                        display: flex;
                                        flex-direction: row;
                                        align-items: center;
                                        text-decoration: none;
                                        justify-content: space-around;
                                        .icon-write{
                                            // display: inline-block;
                                            width: 2.8rem;
                                            height: 2.8rem;
                                         
                                        }
                                        span{
                                            text-decoration: none;
                                            font-size: 1.8rem;
                                            // font-family: $subtext-fontfam;
                                        }
                                    }
                             
                                }
                                .ebac{
                                    height: inherit;
                                    // background-color: springgreen;
                                 
                                    float: right;
                                    margin-left: 1rem;

                                }

                                .topbar-nav-fold{
                                    width: 0rem;
                                    height: 4rem;
                                    // background-color: lightcoral;
                                    float: right;
                                    display: flex;
                                    flex-direction: column;
                                    align-items: center;
                                    justify-content: space-around;
                                    position: relative;
                                    &-dashes{
                                        cursor: pointer;
                                        width: 80%;
                                        height: 60%;    
                                        // background-color: lightgreen;
                                        display: flex;
                                        flex-direction: column;
                                        align-items: flex-end;
                                        // justify-content: space-around;
                                        .ebada{
                                            display: block;
                                            width: 3rem;
                                            height: 0.3rem;
                                            background-color:#444444;
                                            margin-top: 0.4rem;
                                            transform: scale(0);
                                        }
                                    }
                                    &-dashes:hover{
                                        .ebada:nth-child(1){
                                            animation: wave 900ms ease  100;
                                        }
                                        .ebada:nth-child(2){
                                            animation: wave 900ms ease 300ms 100;
                                        }
                                        .ebada:nth-child(3){
                                            animation: wave 900ms ease 600ms 100;
                                        }
                                    }
                                    &-dropdown{
                                        position: absolute;
                                        top: 4.5rem;
                                        left: 0.2rem;
                                        box-shadow: 1px 1px 10px rgba(236,236,236,0.2) ;
                                        z-index: 30;
                                        ul{
                                            li{
                                                a{
                                                    padding-left: 1rem;
                                                    width: 12rem;
                                                    line-height: 4rem;
                                                    display: block;
                                                    height: 4rem;
                                                    border: 1px solid #eee;
                                                    text-decoration: none;
                                                    font-size: 1.6rem;
                                                    font-family: $subtext-fontfam;
                                                    color: $subtext-color;
                                                    background-color: #fff;
                                                }
                                            }
                                        }
                                    }
                            
                                }
                    }
                 }
            }
        }
    }
}

@keyframes wave {
    0%{
        width: 3rem;
    }
    50%{
       width: 1rem;
    }
    100%{
        width: 3rem;
    }
}