@import '../../assests/variables';

.signup-with-email{
    width: 100%;
    height: 100%;
    // background-color: lightblue;

    position: relative;
    animation: popup 300ms ease; 
 
    .aa {    background-color: #fff;
      
        width: 100%;
        height: 80rem;
        // background-color: lightcoral;
        // opacity: 1;
        position: absolute;
        top:50%;
        left:50%;
        transform: translate(-50%,-50%);
        box-shadow: 5px 5px 10px gray;
        .icon-close{
            width: 4rem;
            height: 4rem;
            position: absolute;
            z-index: 20;
            top:2rem;
            right:2rem;
            color:white;
            cursor: pointer;
        }
        .signup-title{
            text-align: center;
            font-weight: 600;
            display: block;
            height: 35%;
            // background-color: darkslateblue;
            div{
                span{
                    display: block;
                }
            }    
        }
      
       .signup-input{
           padding-top: 2rem;
            width: 100%;
            height: 42%;
            // background-color: lightgreen;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: space-around;
            text-align: center;
            font-size: 1.8rem;
            
  
        section:focus-within span{
           visibility: visible;
        }

        section{
            width: 80%;
            height: 4rem;
            line-height: 4rem;
            input{
                outline: 0;
                height: 4rem;
                width: 28rem;
                padding-left: 1rem;
                border-style: none;
                font-size: 1.5rem;
                text-align: center;
                border-bottom: 1px solid #000;
                // box-shadow: 5px 5px 5px gray;
                // border-radius: 1rem;
                }

            span{ 
                visibility: hidden;
                display: block;
                font-family: $subtext-fontfam ;
                color: $subtext-color;
                font-size: 1.2rem;
                transform: translateY(-5px);
            }
        }

        button{
            width: 20rem;
            height: 10%;
            border-style: none;
            border-radius: 2rem;
            background-color:#222222;
            font-weight: 600;
            color: #fff;
            letter-spacing: 0.2rem;
            font-size: 1.6rem;
            margin-top: 0.8rem;
        }
        button:hover{
            cursor: pointer;
        }
    }
    .signup-privacy-policy{
        font-size: 1.2rem;
        input{
            margin-right: 1rem;
            display: inline-block;
            transform:translateY(1px);
            outline: 1;
        }
    }
}

}

@keyframes turnRed{
    0%{
     color:$subtext-color;;
    }
    5%{
    color: red;
    }
    95%{
        color:red;
    }
    100%{
        color:$subtext-color;
    }
}