.userblock-loggedin{
    padding-top: 0.3rem;
    height: 100%;
    width: 80%;
    display: flex;
    flex-direction: row;
    justify-content: right;
    align-items: center;
    // background-color: lightgreen;
    transition: all 0.3s ease;
    section{
        margin-right: 1rem;
    }
    div{
        position: relative;
        span{
        display: block;
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
        cursor:default;
       padding-right: 1rem;
        
    }
        img{
            width: 4.5rem;
            height: 4.5rem; 
            border-radius: 4rem;
            cursor: pointer;
        }
        .avatar-dropdown{
            position: absolute;
            top:5rem;
            left:-2.5rem;
            width: 10rem;
            text-align: center;
            z-index: 20;
            // height: 20rem;
            // background-color: lightgreen;
            overflow:hidden;
            // animation: dropdown 300ms ease;
            ul{
                li{
                    border-radius: 2px;
                    background-color: #fff;
                    border: 1px solid #eee;
                  box-shadow: 1px 1px 10px rgba(236,236,236,0.2) ;
                    line-height: 3rem;
                    display: block;
                    height: 3rem;
                    font-size: 1.6rem;
                    cursor: pointer;
                }
            }
        }
    }
}



.userblock{
    display: flex;
    flex-direction: row;
    justify-content: right;
    align-items: center;

    span{
        // background-color: seagreen;
        margin-right: 8%;
        line-height: 250%;
        width: 9rem;
         height: 80%;
         cursor: pointer;
        text-decoration: none;
    }
    .ebaca{ 
        color: $subtext-color;
        text-align: right;
        font-size: 1.5rem;    
;
    }
    .ebacb{
        background-color: $tag-background;
        text-align: center;
        border-radius: 2rem;
        color: #fff;
        font-weight: 600;
        // padding: 0 0.5rem;
    }
}

.popup-block{
    z-index: 20;
    position: fixed;
    top:0;
    left: 0;
     width: 100vw;
     height: 100vh;
    //  border: 1px solid green;
    background-color: rgba(256,256,256,0.6);


  .aa{
      width: 80rem;
      height: 100%;
      margin: auto;
      position: relative;
    // background-color: seagreen;
    
  }
}

