.blog-title:empty:before{
    content: "Title...";
    padding-left: 0.3rem;
    pointer-events: none;
    color: $subtext-color;
    display: block; /* For Firefox */
}

.editor{
    margin-top: 8vh;
    width: 100%;
    .publish-block{
        width: 100%;
        position: absolute;
        top:0;
        left: 0;
    }
    .ba{
        width: 70%; 
        max-width: $max-width*0.6;
        height: 90vh;

        // border: 1px solid green;
        margin: auto;
        padding-left: 1.5rem;
        padding-right: 1.5rem;
        position: relative;
        padding-bottom: 3rem;
        .cb{
            width: 100%;
            height: 3rem;

            // border: 1px solid red;
            text-align: right;
            .cbb{
                cursor: pointer;
                font-size: 1.6rem;
                // font-weight: 600s;
                display: inline-box;
                color: #fff;
                background-color: seagreen;
                border-style: none;
                padding: 0.5rem 2rem;
                border-radius: 2rem;

            }
        }
        .ca{
            width: 100%;
            // height: 14rem;
            // background-color: lawngreen;
            padding-top: 2rem;
            padding-bottom: 1rem;
          
            
            .da{
                width: 100%;
                outline: 0;
                margin-bottom: 1rem;
                .daa{
                    width: 100%;
                    // background-color: hotpink;
                    display: block;
       
                }
            }

            // .db:empty:before{
            //     content:"sdds";
            //     }
            .db{
                outline: 0;
                padding-top: 2rem;
                font-size: 2.2rem;
                letter-spacing: 0.03rem;
                blockquote{
                    margin-top: 1rem;
                    display: block;
                    width: 100%;
                    border-left: 3px solid $subtext-color;
                    color: $subtext-color;
                    font-family: $subtext-fontfam;
                    font-weight: 500;
                    padding-left: 3rem;
                    font-style: italic;
                }
                code:empty::after{
                    content: 'Please copy your entire code here as plain text...';
                    font-size: 16px;
                    color: #aaa;

                }
  
                code{
                    padding: 1rem 1rem 1rem 2rem;
                    background-color: #ddd;
                    display: block;
                    font-size: 1.6rem;
                   width: 100%;
                    color:rgb(9, 9, 112)!important;
                    margin-top: -1rem;
                    margin-bottom: -1rem;
                    line-height: 2rem;
                    border-radius: 1.5rem;
                    overflow-wrap: break-word;
                }
            
                p,div{

                    line-height: 3.5rem;
                    margin-top: 2rem;
                    margin-bottom: 3rem;
                    outline: 0;
                    word-break:keep-all;
                    color: $text-color;
                }
                h3{
                    margin-bottom: 2rem;
                }
                iframe{
                    width: 100%;
                    min-height: 200px;
                    outline-style: none;
                    border-style: none;
                    scrollbar-width: none; /* Firefox */
                    -ms-overflow-style: none; /* IE 10+ */
                }
                iframe::-webkit-scrollbar {
                    display: none; /* Chrome Safari */
                  }
                figure{
                    padding-top: 1rem;
                    margin-bottom: 2rem;
                    // border: 1px solid green;
                     section{
                        //  border: 1px solid hotpink;
                         text-align: center;
                         img{
                             display: inline-block;
                             max-width: 100%;
                             max-height: 40rem;
                         };
                     }
                     figcaption{
                         outline: none;
                        text-align: center;
                        content:"Image Caption Here...(optional)";
                        display: block; /* For Firefox */
                        color: $subtext-color;
                        font-family: $subtext-fontfam;
                        font-size: 1.4rem;
                     }
                     figcaption:empty::after{
                        outline: none;
                         text-align: center;
                        content:"Image Caption Here...(optional)";
                        display: block; /* For Firefox */
                        color: $subtext-color;
                        font-family: $subtext-fontfam;
                        font-size: 1.4rem;
                     }
                }
            
                p:empty::after{
                    content:"Your text here...(if you want to paste copied text please paste as plain text)";
                    display: block; /* For Firefox */
                    color: $subtext-color;
                    padding-left: 0.3rem;   
                }

                a{
                    color: $text-color;
                }
                a:hover{
                    color: seagreen;
                    cursor: pointer;
                }

            }
        }
    }

}
