@import '../../assests/variables';
.contact{
    width: 100%;
    height: 100%;
    padding-top: 10vh;
    position: relative;
    padding-bottom: 8vh;
    &-pos{
        width: 130rem;
        height: 100%;
        max-width: $max-width;
        padding: 0rem 5rem 0rem 0rem;
        margin:auto;

        margin-top: 5rem;
   
        box-shadow: 5px 5px 5px #ddd;
        // background-color: blue;
        &-bg{
            width: 100%;
            height: 100%;
            background-color: #222222;
            // position: relative;
        }
    }
    &-img{
        // background-color: brown;
        // transform: scale(1.8);
        width: 65rem;
        height: 85rem;
        float: left;

}
    &-container{
        z-index: 40;
        width: 60rem;
        float: right;
        background-color: #fff;
        padding:5rem 0rem 5rem 4rem;
        h1{
            display: block;
            font-size: 3.5rem;
            background-color:rgba(200,200,200,0.4);
            padding:1rem 1rem;
            // border-right: 3rem solid #222222;
        }
        p{  padding-top: 1rem;
            line-height: 2.8rem;
            font-size: 2rem;
            font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
            color: #505050;
        }
        .contact-input{
            position: relative;
            margin-top: 2rem;
            input{
                font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
                // background-color: #eee;
                border-style: none;
                display: block;
                margin-bottom: 1rem;
                font-size: 1.8rem;
                padding:0rem 1rem;
                width: 30rem;
                height: 4rem;
                border:1px solid #ddd ;
                box-shadow: 2px 2px 2px #ddd;
            }

            &-description{
                font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
                border-style: none;
                width: 50rem;
                height: 20rem;
                font-size: 2rem;
                padding:1rem 1rem;
                box-shadow:2px 2px 2px #ddd;
                border:1px solid #ddd ;
                
            }
            button{
                border-style: none;
                font-size: 2rem;
                display: block;
                padding: 0.5rem 2rem;
                border-radius: 2rem;
                margin-top: 1rem;
                cursor: pointer;
                margin-bottom: 2rem;
                background: #222;
                color: #fff;
                font-weight: 600;
                
            }
            &-feedback{
                z-index: 100;
                position: fixed;
                width: 100vw;
                height: 100vh;
                left: 0;
                top:0;
                background-color: rgba(256,256,256,0.4);
                display: flex;
                flex-direction: row;
                align-items: center;
                justify-content: space-around;
                &-container{
                    box-shadow: 5px 5px 5px #525252;
                    text-align: center;
                    font-size: 2rem;
                    width: 30rem;
                    height: 20rem;
                    color:#fff;
                    background-color: #222222;
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    justify-content: space-around;
                    padding: 2rem 2rem;
                    animation: fadeInUp 1s ease-in-out;
                    span{
                        display: block;
                        padding-top: 1rem;
                    }
                    button{
                        background-color: seagreen;
                    }
                }
           
            }
        }
        .contact-socialmedia{
            width: 20rem;
            height: 5rem;
            display: flex;
            flex-direction: row;
            justify-content: space-around;
            align-items: center;
            margin-top: 1rem;
            // margin: auto;
            &-icon1{
              transform: scale(1.18);
            }
            &-icon2{
              transform: scale(0.9);
            }
            &-icon3{
              transform: scale(0.95);
            }
            &-icon4{
              transform: scale(1.05);
            }
            &-icon5{
              transform: scale(1.2);
            }
            a{
              cursor: pointer;
              color: rgba(100,100,100,0);
              svg{
                width: 3rem;
                height: 3rem;
              }
            }
        }
    }
}