@import '../../assests/variables';
.signup-title{
           
    h1 {
        font-family: $title-fontfam2;
        color: $text-color;
        font-size: 5rem;
        line-height: 30rem;
        z-index: 10;
        color: #fff;
        font-size: 4rem;
        mix-blend-mode: difference;
      }
      .hero {
            // background-color: lightcoral;
        position: relative;
        width: 100%;
        height: 100%;
        background-color: #eee;
      }
      .svg {
          position: absolute;
          top:0;
          left:0;
          height: 100%;
        width: 100%;
        z-index: 0;
        isolation: isolate;
        // background-color: darkseagreen;
        circle,
        rect {
          fill: #fff;
          mix-blend-mode: exclusion;
        }
      }
      #Oval1 {
        animation: oval1 16s infinite ease-in-out;
      }
      @keyframes oval1 {
        0% {
          cx: 800;
          cy: 260;
          r:160;
        }
        25% {
          cx: 200;
          cy: 80;
        }
        50% {
          cx: 120;
          cy: 450;
          r:70;
        }
        75% {
          cx: 300;
          cy: 240;
        }
        100% {
          cx: 800;
          cy: 260;
          r:160;
        }
      }
      
      #Oval2 {
        animation: oval2 12s infinite ease-in-out;
      }
      @keyframes oval2 {
        0% {
          cx: 500;
          cy: 300;
        }
        25% {
          cx: 700;
          cy: 400;
          r:80;
        }
        50% {
          cx: 250;
          cy: 100;
        }
        75% {
          cx: 0;
          cy: 100;
          r:160;
        }
        100% {
          cx: 500;
          cy: 300;
         
        }
      }
  
      #Rectangle1 {
        animation: rectangle1 10s infinite ease-in-out;
      }
      @keyframes rectangle1 {
        0% {
          x: 480;
          y: 260;
        }
        30% {
          x: 800;
          y: 0;
          width: 250px;
        }
        70% {
          x: 50;
          y: 200;
          width: 150px;
          height: 260px;
        }
        100% {
          x: 480;
          y: 260;
        }
      }
      #tri {
        animation: rectangle1 9s infinite ease-in-out;
      }
      @keyframes tri {
        0% {
          x: 380;
          y: 60;
        }
        30% {
          x: 700;
          y: 0;
          width: 200px;
        }
        70% {
          x: 100;
          y: 100;
          width: 150px;
          height: 60px;
        }
        100% {
          x: 380;
          y: 60;
        }
      }
}