@import '../../assests/variables';
.aboutme{
    width: 100%;
    padding-top: 9vh;
    background-color: #fff;
    // padding-bottom:10rem;
    // background-color: fuchsia;
    &-container{

        width: 100%;
        padding: 0rem 5rem;
        margin: auto;
        max-width: $max-width;
        min-width: 1300px;
        position: relative;
      //  overflow-x:hidden ;
    }
    &-mobile{
      width: 100%;
      text-align: center;
      padding-bottom: 3rem;
      opacity: 0.4;
      animation: fadeInDown 1s forwards;
      display: none;
      span{
       
        font-size: 2rem;
      }
    }
    &-hoverme{
      transform: translateY(-1000px);
      width: 100%;
      text-align: center;
      padding-bottom: 1rem;
      opacity: 0.7;
      animation: fadeInDown 1s forwards;
      span{
        font-size: 2rem;
      }
      &-down{
        margin-left: 1rem;
        display: inline-block;
        border-left: 1rem solid transparent;
        border-right: 1rem solid transparent;
        border-top: 1.5rem solid #222222;
        animation: bounce 1s ease 1.5s 3;
      }
    }
    &-contact{
      z-index: 10;
      width: 5rem;
      height: 20rem;
      position: absolute;
      top: 55rem;
      right:-2rem;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      align-items: center;
      animation: fadeInDown 1s forwards;
      &-icon1{
        transform: scale(1.18);
      }
      &-icon2{
        transform: scale(0.9);
      }
      &-icon3{
        transform: scale(0.95);
      }
      &-icon4{
        transform: scale(1.05);
      }
      &-icon5{
        transform: scale(1.2);
      }
      a{
        cursor: pointer;
        color: rgba(100,100,100,0);
        svg{
          width: 3rem;
          height: 3rem;
        }
      }
    }

  
