@keyframes moveAround1 {
  from{
    transform: translate(-40px,-30px);
  }
  to{
    transform: translate(-40px,-30px);
  }
  0%,
  50%,
  100%{
    transform: translate(0px,0px);
  }
  5%,
  55%{
    transform: translate(5px,-7px);
  }
  10%,
  60%{
    transform: translate(10px,-5px);
  } 
  15%,
  65%{
    transform: translate(3px,0px);
  }
  20%,
  70%{
    transform: translate(5px,5px);
  } 
  25%,
  75%{
    transform: translate(0px,12px);
  }
  30%,
  80%{
    transform: translate(-10px,10px);
  }   
  35%,
  85%{
    transform: translate(-2px,2px);
  }
  40%,
  90%{
    transform: translate(-5px,5px);
  } 
  45%,
  95%{
    transform: translate(-3px,3px);
  }

}
@keyframes moveAround2 {
  from{
    transform:  rotateY(180deg) rotate(-30deg) translate(-40px,-30px);
  }
  to{
    transform:   rotateY(180deg) rotate(-30deg) translate(-40px,-30px);
  }
  0%,
  50%,
  100%{
    transform:   rotateY(180deg) rotate(-28deg) translate(0px,0px);
  }
  5%,
  55%{
    transform:   rotateY(180deg) rotate(-30deg) translate(5px,5px);
  }
  10%,
  60%{
    transform:   rotateY(180deg) rotate(-28deg) translate(10px,3px);
  } 
  15%,
  65%{
    transform:  rotateY(180deg) rotate(-30deg) translate(3px,0px);
  }
  20%,
  70%{
    transform:  rotateY(180deg) rotate(-26deg) translate(5px,5px);
  } 
  25%,
  75%{
    transform: rotateY(180deg) rotate(-30deg) translate(0px,8px);
  }
  30%,
  80%{
    transform:  rotateY(180deg) rotate(-26deg) translate(-10px,9px);
  }   
  35%,
  85%{
    transform: rotateY(180deg) rotate(-30deg) translate(-2px,2px);
  }
  40%,
  90%{
    transform:  rotateY(180deg) rotate(-28deg) translate(-5px,5px);
  } 
  45%,
  95%{
    transform:  rotateY(180deg) rotate(-30deg) translate(-3px,3px);
  }

}

