.oAuth-login{
    height: 20%;
    padding-top: 0.5rem;
    background-color: #fff;
   section{
        width: 100%;
        background-color: #fff;
        span{
            text-align: center;
            display: block;
            width: 60%;
            margin:auto;
            // background-color: lightcoral;
            font-size: 2rem;
            padding-top: 1rem;
        }
        .dots{
            font-size: 2rem;
            font-weight: 600;
            letter-spacing: 0.4rem;
        }
    }
 
    div{
        margin: auto;
        width: 40rem;
        padding-top: 2rem;
        background-color: #fff;
        // background-color: lightgreen;
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        span{
            display: block;
            width: 5rem;
            height: 5rem;
            // border: 1px solid lightsalmon;
            a{
                display: block;
               width: 100%;
               height: 100%;
            //    background-color: lightsalmon;
               .icon-oauth{
                   width: 100%;
                   height: 100%;
               }
            }
           .icon-oauth-facebook{
               
               width: 100%;
               height: 100%;
               transform: scale(1.25);
           }
        }
    }
}