@import '../../assests/variables';


.blog{
    // margin-top: 5rem;
    width: 100%;
    padding-top: 6vh;
    .ba{
        width: 60%; 
        max-width: $max-width*0.6;
        // border: 1px solid green;
        margin: auto;
        padding-left: 1.5rem;
        padding-right: 1.5rem;
        min-height: 90vh;
        .ca{
            width: 100%;
            // height: 14rem;
            // background-color: lawngreen;
            padding-top: 2rem;
            padding-bottom: 1rem;
            
        
            .da{
                width: 100%;
                line-height: 5rem;
                text-align: left;
                    .daa{
                        // background-color: blueviolet;
                                margin-bottom: 1rem;
                    }
                }
                .db{
                    // padding-top:1rem ;
                    // background-color: mediumvioletred;
                    .dba{
                       color: #fff;
                        display: block;
                        padding:0.5rem 1rem;
                        background-color: seagreen;
                        float:left;
                        border-radius: 2rem;
                        margin: 0.5rem 0.5rem 0.5rem 0;
                        font-size: 1.4rem;
                    }
                }
            }
        .cb{
            width: 100%;
            // height: 8rem;
            // background-color: lightcoral;
            padding-top: 1rem;
            padding-bottom: 1rem;
            .cba{
                float: left;
                width: 6rem; 
                height: 6rem;
                border-radius: 6rem;
                // border:2px dashed seagreen;
            }
            .cbb{
                float: left;
                width: 16rem;
                height: 6rem;
                border:1px solid oragne;
                // background-color: lightblue;
                padding-left: 2rem;
                .cbba{
                    font-weight: 530;
                    margin-top: 1rem;
                }
                .cbbb{
                    font-weight: 530;
                    margin-top: 1rem;
                }
            }
            .cbc{
                width: 20rem;
                height: 6rem;
                // background-color: mediumspringgreen;
                float: right;
                display: flex;
                flex-direction: row;
                align-items: center;
                justify-content: space-around;
                .icon-share{
                    width: 3.5rem;
                    height: 3.5rem;
                    cursor: pointer;
                }
            }
        }
        .cc{
            outline: 0;
            padding-top: 2rem;
            line-height: 3.5rem;
            letter-spacing: 0.03rem;
            // code{
            //     display: block;
            //     font-size: 1.6rem;
            //     padding: 1rem 1rem 1rem 2rem;
            //     width: 100%;
            //     border-radius:1rem ;
            //     color:rgb(9, 9, 112)!important;
            //     background-color: #ddd;
            //     line-height: 2.4rem;
            //     margin-bottom: -1rem;
            //     margin-top:-1rem ;
            // }
        
            blockquote{
                margin-top: 1rem;
                display: block;
                width: 100%;
                border-left: 3px solid $subtext-color;
                color: $subtext-color;
                font-family: $subtext-fontfam;
                font-weight: 500;
                padding-left: 3rem;
                font-style: italic;
            }
            iframe{
                width: 100%;
                min-height: 200px;
                outline-style: none;
                border-style: none;
                scrollbar-width: none; /* Firefox */
                -ms-overflow-style: none; /* IE 10+ */
            }
            iframe::-webkit-scrollbar {
                display: none; /* Chrome Safari */
              }
              code{
                padding: 1rem 1rem 1rem 2rem;
                background-color: #ddd;
                display: block;
                font-size: 1.6rem;
               width: 100%;
                color:rgb(9, 9, 112)!important;
                margin-top: -1rem;
                margin-bottom: -1rem;
                line-height: 2rem;
                border-radius: 1.5rem;
            }
        
            code{
                padding: 1rem 1rem 1rem 2rem;
                background-color: #ddd;
                display: block;
                font-size: 1.6rem;
               width: 100%;
                color:rgb(9, 9, 112)!important;
                margin-top: -1rem;
                margin-bottom: -1rem;
                line-height: 2rem;
                border-radius: 1.5rem;
                overflow-wrap: break-word;
            }
        
            p,div{

                line-height: 3.5rem;
                margin-top: 2rem;
                margin-bottom: 3rem;
                outline: 0;
                word-break:keep-all;
                color: $text-color;
            }
            h3{
                margin-bottom: 2rem;
            }
            figure{
                padding-top: 1rem;
                margin-bottom: 2rem;
                 section{    
                     text-align: center;
                     img{
                         display: inline-block;
                         max-width: 100%;
                         max-height: 40rem;
                     };
                 }
                 figcaption{
                     outline: none;
                    text-align: center;
                    content:"Image Caption Here...(optional)";
                    display: block; /* For Firefox */
                    color: $subtext-color;
                    font-family: $subtext-fontfam;
                    font-size: 1.4rem;
                 }
                //  figcaption:empty::after{
                //     outline: none;
                //      text-align: center;
                //     content:"Image Caption Here...(optional)";
                //     display: block; /* For Firefox */
                //     color: $subtext-color;
                //     font-family: $subtext-fontfam;
                //     font-size: 1.4rem;
                //  }
            }
        
            // p:empty::after{
            //     content:"your text here...";
            //     display: block; /* For Firefox */
            //     color: $subtext-color;
            //     padding-left: 0.3rem;   
            // }

            a{
                color: $text-color;
            }
            a:hover{
                color: seagreen;
                cursor: pointer;
            }

        }
    }
    
}