@import '../../assests/variables';
@mixin center{
    position: relative;
    top:50%;
   left:50%;
   transform:translate(-25%,-25%)

}
.blog-list{
    background-color: #fff;
    position: relative;
    width: 100%;
    min-height: 92vh;
    padding-top: 6vh ;
    // padding-bottom: 10rem;
    .blog-list-container{
        margin:auto;
        width: 60%;
        // height: 80%;
        max-width: $max-width * 0.6;
        padding-top: 3rem;
        // background-color: rgb(250,250,250);
        // background-color: darkseagreen;
        .blog-list-header{
            height: 8rem;
            font-size: 4rem;
            border-bottom: 1px solid #222222;
        }
    }
    &-blogs::-webkit-scrollbar {
        display: none; /* Chrome Safari */
      }
    &-blogs{
        width: 100%;
        .blog-list-blog:hover{
            // transform: translate(2px,-6px);
            // box-shadow: 5px 5px 5px #ddd;
        }

        .blog-list-blog{

            margin-top: 5rem;
            // background-color: #eee;
            width: 100%;
            // height: 10rem;
            &-title{
                text-decoration: none;
                float: left;
                width: 100%;
                font-size: 2.4rem;
               margin-top: 2rem;
                color:#222222;
            }
            &-abstract{
                text-decoration: none;
                width: 100%;
                float: left;
                font-weight:500;
                font-size: 1.6rem;
                margin-top: 1.2rem;
                text-overflow: ellipsis;
                white-space: wrap;
                overflow: hidden;
                font-family: $subtext-fontfam;
                color: $subtext-color;
            }
            &-img{
                width: 100%;
                height: 40rem;
                float: right;
                background-color: #fff;
                // background-color: lightgreen;
                overflow: hidden;
                display: flex;
                flex-direction: row;
                align-items: center;
                justify-content: space-around;
                img{
                // max-height: 100%;
                max-width: 100%;
                }
            }
            &-footer{
                margin-top: 2rem;
                margin-bottom: 1rem;
                // position: absolute;
                // bottom: 0;
                width: 100%;
                height: 4rem;
                // background-color: lightskyblue;
                float: left;
                display: flex;
                flex-direction: row;
                align-items: center;
                img{
            
                    height: 4rem;
                    width: 4rem;
                    border-radius: 50%;
                }
                    
                    span{ 
                        margin-left: 0.5rem;
                       display: block;
                        font-size: 1.6rem;
                        font-family: $subtext-fontfam;
                        color:$subtext-color;
                    }
                    span:first-of-type{
                        font-size: 1.8rem;
                        font-weight: 550;
                        color: #333333;
                        
                    }
             
                
        
                &-tags{
                    // float: right;
                   font-weight: 600;
                    margin-left:3rem !important;
                     padding: 0.5rem 1rem;
                     background-color: #222222;
                     border-radius: 2rem;
                     color: #fff!important ;
                    //  letter-spacing: 0.05rem;
                }
            }
            &-edit{
                position: relative;
                &-delete-msg{
                    border:1px solid #ddd;
                    box-shadow: 5px 5px 5px #ddd;
                    top:-20rem;
                    left:-20rem;
                    position: absolute;
                    width:30rem;
                    height:20rem;
                    background-color:#fff;
                    padding: 3rem 2rem;
                 
                   &1{ text-align: center; 
                       margin-left: 0!important;
                        width: 100%;
                        // background-color: royalblue;
                        display: block;
                        margin-bottom: 2rem;
                    }
                    &2{
                        margin-left: 1rem!important;
                        float: left;
                        display: block;
                        padding: 1rem 2rem;
                        background-color: seagreen;
                        border-radius: 2rem;
                        font-weight: 600;
                        color: #fff!important;
                        cursor: pointer;
                    }
                    &3{
                        cursor: pointer;
                        float: left;
                        margin-left: 5rem!important;
                        display: block;
                        padding: 1rem 2rem;
                        background-color: #222222;
                        border-radius: 2rem;
                        font-weight: 600;
                        color: #fff!important;
                    }
                    &4{
                        display: block;
                        float: right;
                        padding-right: 2rem;
                        padding-top: 1rem;
                    }
                }
                // overflow: hidden;
                width: 7rem;
                height: 4rem;
                // background-color: darkturquoise;
                margin-left: 2rem;
                display: flex;
                flex-direction: row;
                align-items: center;
                justify-content: space-around;
               div{
                width: 4rem;
                height: 100%;
                // background-color: firebrick;
                display: flex;
                flex-direction: row;
                align-items: center;
                justify-content: space-around;
                cursor: pointer;
                .icon-delete{
                    width: 2.5rem;
                    height: 2.5rem;
                }
                .icon-edit{
                    width: 2.3rem;
                    height: 2.3rem;
                }
               }
            }
            
        }
    }
}