@import '../../assests/variables';
.publish-blog{

    width: 100%;
    height: 100vh;
    background-color: rgba(256,256,256,0.6);
    .ba{
        width: 60rem;
        height: 100%;
        margin: auto;
        // background-color: darkseagreen;
        display: flex;
        align-items: center;
        .publish-blog-container{
            animation: popup 300ms ease; 
            position: relative;
            width: 100%;
            height: 60rem;
            background-color:#fff;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: space-around;
            box-shadow: 5px 5px 10px #ccc;
            .icon-close{
                width: 3rem;
                height: 3rem;
                position: absolute;
                top:1rem;
                right:1rem;
                cursor: pointer;
            }
            h1{
                font-family: $title-fontfam;
                font-size: 3rem;
            }
            .publish-blog-title{
                width: 80%;
                font-family: $text-fontfam;
                font-size: 2.4rem;
            }

            .publish-blog-abstract{
                width: 80%;
                height: 8rem;
                outline:0;
                border-style: none;
                // height: 5rem;
                textarea{
                    width: 100%;
                    height: 8rem;
                    padding-top: 0.5rem;
                    padding-left: 1rem;
                    font-size: 1.6rem;
                    font-family: $subtext-fontfam;
                }
               
                
            }
            .publish-blog-uploadimg{

                width: 80%;
                height:8rem;
            
                text-align: center;
                position: relative;
                .publish-blog-err{
                   text-align: center;
                    &-msg{
                        margin-left: 12rem;
                        padding: 0rem 1rem;
                        font-size: 1.4rem;
                        color: rgb(253, 46, 46);
                        font-weight: 600; 
                    }
                    // animation: appear-disappear 3s ease-in-out forwards;
                }
                input{
                    width: 40%;
                    // background-color: slateblue;
                    display: inline-block;
                    line-height: 8rem;
                    cursor: pointer;
                    // outline: red;
                }
                span{
                    font-family: $subtext-fontfam;
                    color: $text-color;
                    font-size: 1.4rem;
                    font-weight: 600;
                    position: absolute;
                    left:0;
                    // padding-bottom: 1rem;
                  

                }
            }
            .publish-blog-submit{
              button{
                  width: 15rem;
                  padding-top: 1rem;
                  padding-bottom: 1rem;
                  border-style: none;
                  background-color: seagreen;
                  border-radius: 2rem;
                  color: #fff;
                  font-weight: 600;
                  cursor: pointer;
                //   letter-spacing: 0.1rem;
              }
              span{
                display: block;
                margin-top: 2rem;
                font-size: 1.6rem;
                width: 100%;
                text-align: center;
                font-weight: 600;
              }
              span:last-of-type{
                  color: rgb(253, 46, 46);
              }
            }
            .publish-blog-tags{
                position: relative;
                width: 80%;
                // background-color: lightgreen;
                text-align: center;
                &-text{
                    display: block;
                    position: absolute;
                  line-height: 3rem;
                  font-size: 1.4rem;
                  font-family: $subtext-fontfam;
                  font-weight: 600;
                
                }
                ul:empty::after{
                    content:"Please add some tags to your blog, up to 5 tags!";
                    text-align: left;
                    line-height: 3rem;
                    font-family: $subtext-fontfam;
                    color: $subtext-color;
                    font-size: 1.4rem;
                }
                div{
                    min-height: 3rem;
                    width: 100%;
                    // background-color: skyblue;
                    margin-bottom: 0.6rem;
                    ul{ 
                        width: 80%;
                        display: block;
                        margin-left: 4.5rem;
                        // float: left;
                        li{
                            display: block;
                            height: 3rem;
                            float: left;
                            border:1px solid rgba(0,0,0,0);
                            background-color: seagreen;
                            margin-bottom: 0.5rem;
                          margin-right: 0.5rem;
                          display: flex;
                          flex-direction: row;
                          align-items: center;
                          border-radius: 2rem;

                         span{
                          
                            font-family: $subtext-fontfam;
                            font-size: 1.2rem;
                            font-style: bold;
                            color:#fff;
                         }
                         span:first-child{
                            line-height: 3rem;
                             padding-left: 1rem;
                         }
                         span:last-child{
                             display: block;
                             width: 1.6rem;
                             height: 1.6rem;
                             border-radius: 1.6rem;
                             margin-left: 0.4rem;
                             margin-right:0.4rem;
                             background-color: rgba(22,22,22,0.3);
                            cursor: pointer;
                            //  font-size: 1.6rem;
                         }
                        }
                    }
                }
                input{
                    display: block;
                    font-size: 1.2rem;
                    width: 15rem;
                    border-style: none;
                    border:1px solid #ccc;
                    height: 3rem;
                    padding-left: 0.8rem;
                    margin-left: 10%;
                    border-radius: 0.3rem;
                    position: relative;
                    // margin: auto;
                }
            }
            
        }
    }
}