
@keyframes appear-disappear {
    0%{
        opacity: 0;
    }
    20%{
        opacity: 1;
    }
    80%{
        opacity: 1;
    }
    100%{
        opacity: 0;
    }
}
.blog-add{
    // background-color: #eee;
    // position: absolute
    width: 16rem;
    height: 4rem;
    position: relative;
    left:-18rem;
    margin-bottom: 6rem;
    // padding-top: 1rem;
    // float: right;
    &-err{
        position: absolute;
        top:6rem;
        left:-2rem;
        &-msg{
            display: block;
            width: 120%;
            height: 100%;
            background-color: rgba(218, 123, 123, 0.74);
            padding: 1rem 1rem;
            font-size: 1.6rem;
            color: rgb(253, 46, 46);
            font-weight: 600; 
        }

        animation: appear-disappear 3s ease-in-out forwards;
    }
    &-info{
        position: absolute;
        top:3rem;
        left:3rem;
        &-msg2{
            display: block;
            width: 100%;
            height: 100%;
            // background-color: rgba(218, 123, 123, 0.74);
            padding: 1rem 1rem;
            font-size: 1.6rem;
            color: #222222;
            font-weight: 600; 
        }
    }
 
    &-icon{
    float: right;
    transition: all 400ms ease;
      .icon-add{
          width: 4rem;
          height: 4rem;
        //   background-color: seagreen;
          cursor: pointer;
      }
      .icon-add::hover{
         background-color: seagreen;
      }
    }
    
    .blog-add-buttons{
        float: right;
        width: 16rem;
        height: 4rem;
        overflow: hidden;
        // background-color?: #eee;
        transition: all 400ms ease ;
        .blog-add-img{
    
            margin-right: 1rem;
            width: 4rem;
            height: 4rem;
            float: right;
            overflow: hidden;
            // background-color: violet;
            span{
               z-index: -10;
                .icon-insertPic{
                    width: 4rem;
                    height: 4rem;
                    cursor: pointer;
                }
            }
            input{
                width: 10rem;
                height: 10rem;
            //    position: absolute;
                // opacity: 0;
                // border: 10px solid red;
                background-color: violet;
                cursor: pointer;
                transform: translateY(-6rem);
                opacity: 0;
                // z-index: 10;
            }
        }


        .blog-add-code{
            overflow: hidden;
            margin-right: 1rem;
            float: right;
            width: 4rem;
            height: 4rem;
            // background-color: skyblue;
            .icon-addcode{
                width: 4rem;
                height: 4rem;
                cursor: pointer;
            }
            div{
                position: absolute;
                width: 40rem;
                // background-color: violet;
                margin-top: 1rem;
                input{
                    float: left;
                    height: 3rem;
                    padding-left: 1rem;
                    padding-right: 0.5rem;
                    border-style: none;
                    background-color: raba(256,256,256,0);
                    
                }
                input:first-of-type{
                    border:2px solid #ddd;
                    border-radius: 2rem 0 0 2rem;
                    width: 20rem;
                }
                input:last-of-type{
                //    border-top: 2px;
                border-left: 0;
                border-right: 0;
                   border-style: solid;
                   border-color: #ddd;
                    width: 11rem;
                    
                }
                button{
                    float: left;
                    border:2px solid #ddd;
                    height: 3rem;
                    width: 6rem;
                    background-color: seagreen;
                    border-style: none;
                    color: #fff;
                    font-size: 1.4rem;
                    font-weight: 500;
                    border-radius:0 2rem 2rem 0;
                    cursor: pointer;
                }
            }
        }
     
    }
   
}
