@import '../../assests/_variables.scss';
.home{
    width: 100%;
    // background-color: lightgray;
    max-width: $max-width;
    min-width: 120rem;
    padding: 0rem 5rem;
    margin: auto;

    .ba{
       position: relative;
        padding-top: 7vh;
        height:100%;
        padding-bottom: 10rem;
        // background-color: pink;
        .container-right{
            float: right;
            width: 40%;
            height: 100rem;
            // background-color: skyblue;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content:space-around;
            div{
                border-radius: 1rem;
                background-color: #eee;
                width: 100%;
                // height: 40rem;
                padding: 1rem 1rem;
                h1{
                    padding-left: 1rem;
                    font-size: 3rem;
                    color: #222222;
                }
                // background-color: slateblue;
                ul{width: 100%;
                    li{
                        display: block;
                        width: 100%;
                        margin-top: 1.5rem;
                        overflow-wrap: break-word;
                    
                        a   {
                            line-height: 3.1rem;
                            // text-decoration: none;
                            color:#222222;
                            display: block;
                            font-size: 2.2rem;
                            width: 100%;
                            height: 60%;
                        }
                        div{
                            // background-color: turquoise;
                            width:100%;
                            height: 40%;
                            display:flex;
                            flex-direction: row;
                            align-items: center;
                            padding-top: 1rem;
                            img{
                                margin-left: -1rem;
                                width: 4rem;
                                height: 4rem;
                                border-radius: 50%;
                            }
                            span{margin-left: 1rem;
                                font-size: 1.4rem;
                                font-family: $subtext-fontfam;
                                color: $subtext-color;
                            }
                            .homepage-right-tags{
                                font-family:Franklin Gothic Medium, Helvetica, sans-serif;
                                padding: 0.5rem 1rem;
                                background-color: $tag-background;
                                color: #fff;
                                border-radius:2rem ;
                          
                            }
                        }
                    }
                }
            }
        }

        .container-top{

            width: 100%;
            height: 100%;
            animation: turnBlack 0ms ease 3900ms forwards,changeWhite1 0ms ease 6900ms forwards, turnBlack 0ms ease 9250ms forwards,changeWhite1 0ms ease 14150ms forwards;
        }
        .container-left{
            width: 58%;
            height: 100%;
            float: left;
            // background-color: deepskyblue;
            padding:1rem  2rem 10rem 2rem;
            .home-introduction{
                hr{
                    margin-top: 2rem;
                }
                line-height: 3rem;
                // text-align: justify;
                font-size: 2rem;
                font-family: Andale Mono, Helvetica, Tahoma, sans-serif;
                ul{
                    li{ margin-left: 3rem;
                        font-size: 1.8rem;
                        list-style:inside square;
                        font-family: 'Lucida Sans Unicode', Helvetica,'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
                        font-style: italic;
                    }
                }
                p{width: 100%;
                    display: block;
                    a{
                        color:#222222
                    }
                    h1{
                        line-height: 6rem;
                        font-size: 6.5rem;
                        // display: block;
                        font-weight: 800;
                        margin-left:1rem ;
                        margin-right: 1.5rem;
                        float: left;
                        width: 6rem;
                        height: 6rem;
                        // background-color: silver;
                        padding-left:-10rem;
                    }
                    // text-indent: 3rem;
                    span{
                        font-size: 1.6rem;
                        margin-bottom: 1.5rem;
                        margin-left: 1rem;
                        display: inline-block;
                        padding: 0.5rem 1rem;
                        border-radius: 0.5rem;
                        font-weight: 600;
                        background-color: $tag-background;
                        color: #fff;
                    }
               
                    margin-top: 2rem;
                }
            }
        }
    }
}