@import './variables';

.g-title{
    font-size: 2.4rem;
    color: $title-color;
    font-family: $title-fontfam;
}

.g-subtext{
    font-size: 1.5rem;
    color: $subtext-color;
    font-family: $subtext-fontfam;
}

.g-text{
    font-size: 2.2rem;
    color: $text-color;
    font-family: $text-fontfam;
}

.g-blog-title{
    font-size: 4rem;
    color: $title-color;
    font-family: $title-fontfam2;
    font-weight: 500;
   
}


.g-content-section{
    margin-top: 5rem;
    width: 100%;
}

.g-content-container{
    width: 60%; 
    max-width: $max-width*0.6;
    height: 90vh;
    border: 1px solid green;
    margin: auto;
    padding-left: 1.5rem;
    padding-right: 1.5rem;
}